import { combineReducers } from 'redux'
function counter(num = 0, action) {
    switch (action.type) {
        case 'PLUS':
            return num += 1
        case 'MINUS':
            return num -= 1
        default:
            return num
    }
}
function todo(state = [], action) {
    switch (action.type) {
        case 'ADD_TODO':
            return [...state, { id: action.id, text: action.text, done: false }]
        case 'TOGGLE_TODO':
            return state.map(singleTodo => (
                (singleTodo.id === action.id) ? { ...singleTodo, done: !singleTodo.done } : singleTodo
            ))
        default:
            return state
    }
}
let homeDataInit = {
    hot: [
        {
            id: 1,
            title: '兒童夏令營．森林跑站小小運動家培育營',
            content: '位於大安森林公園旁邊的森林跑站，相信常前往大安森林公園運動的朋友們一定不陌生！森林跑站立志打造一個專為跑者而生的運動空間，專業的教室與講者，不但提供專業的諮詢，還有營養滿點的健康飲食，是台北人健身運動的好去處！',
            city: '台北市',
            price: 8000
        }
    ],
    new: [
        {
            id: 1,
            title: 'Miss J Baking Studio - 「光芒」芒果提拉米蘇',
            content: '在 Miss J Baking Studio，你可以來一場不一樣的下午茶時光Miss J Baking Studio 擁有優質烘焙環境與設備，優質的師資細心教你一步步親手烘焙美味甜點。揪樓上樓下的好厝邊、姊妹淘、家人或是同事一起來手作甜點，享受著五星級的烘焙教室及設備，完成後，還可以坐下來喝杯咖啡、吃著自己做的甜點，這是一件多麼幸福的事啊！',
            city: '台北市',
            price: 2600
        }
    ],
    featured:[
        {id:1}
    ]
}
function homeData(state = homeDataInit, action) {
    switch (action.type) {
        case 'GET_DATA':
            return state[action.type].filter(item=> item.id === action.id)
        default:
            return state
    }
}
export default combineReducers({
    todo,
    counter,
    homeData
})
