import React, { Component } from 'react'
import axios from 'axios'
import '../css/calender.scss'

export default class Calender extends Component {
    constructor(props) {
        super(props)
        this.state = {
            year: 2019,
            month: 5,
            eachMonthHaveDay: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
            allMonthDayList: [],
            monthSchedule: [],
            itemData: [],
            dateData: [],
            activedDay: []
        }
    }
    monthChange(value) {
        const month = this.state.month - 1
        let date = new Date(this.state.year, month + value)
        let stateTemp = {
            year: date.getFullYear(),
            month: date.getMonth(),
            eachMonthHaveDay: [...this.state.eachMonthHaveDay]
        }
        if (stateTemp.year % 4 === 0) {
            stateTemp.eachMonthHaveDay[1] = 29
        } else {
            stateTemp.eachMonthHaveDay[1] = 28
        }
        const firstWeekDay = new Date(stateTemp.year, stateTemp.month, 1).getDay()
        let nowDay = 1
        let week = 0
        stateTemp.allMonthDayList = [[]]
        for (let index = 0; index < firstWeekDay; index++) {
            stateTemp.allMonthDayList[0][index] = null
        }
        stateTemp.allMonthDayList[0][firstWeekDay] = 1
        for (let WeekDay = firstWeekDay; WeekDay < 7; WeekDay++) {
            if (nowDay <= stateTemp.eachMonthHaveDay[stateTemp.month]) {
                stateTemp.allMonthDayList[week][WeekDay] = nowDay
                nowDay += 1
                if (WeekDay === 6) {
                    stateTemp.allMonthDayList.push([])
                    week += 1
                    WeekDay = -1
                }
            } else {
                continue
            }
        }
        this.setState({
            allMonthDayList: stateTemp.allMonthDayList,
            month: stateTemp.month + 1,
            year: stateTemp.year,
            eachMonthHaveDay: stateTemp.eachMonthHaveDay
        })
        this.getMonthSchedule(stateTemp.year, stateTemp.month + 1)
    }
    handleClickDay(day) {
        const checkDate = num => {
            const str = num.toString()
            if (str.length === 1) {
                return '0'.concat(str)
            } else {
                return str
            }
        }
        const param =
            `?sub_product_id=${this.props.sub_product_id}&year=${this.state.year}&month=${checkDate(this.state.month)}&day=${checkDate(day)}`

        const self = this
        axios.get(process.env.REACT_APP_BACKHOST + '/get_schedule/' + param)
            .then(response => {
                self.setState({ dateData: response.data })
            })
            .catch(function (error) {
                console.log(error);
            });
    }
    getItems() {
        const param = `?sub_product_id=${this.props.sub_product_id}`
        const self = this
        axios.get(process.env.REACT_APP_BACKHOST + '/getitem/' + param)
            .then(response => {
                self.setState({ itemData: response.data })
            })
            .catch(function (error) {
                console.log(error);
            });
    }
    getMonthSchedule(year, month) {
        const checkDate = num => {
            const str = num.toString()
            if (str.length === 1) {
                return '0'.concat(str)
            } else {
                return str
            }
        }
        const param =
            `?sub_product_id=${this.props.sub_product_id}&year=${year}&month=${checkDate(month)}`
        const self = this
        axios.get(process.env.REACT_APP_BACKHOST + '/get_schedule/' + param)
            .then(response => {
                self.setState({ monthSchedule: response.data })
                const temp = response.data.map(day => {
                    if (day.date[8] === '0') {
                       return day.date[9]
                    }
                    else {
                        return day.date.substr(-2, 2)
                    }
                })
                console.log(temp)
                self.setState({ activedDay: temp })
            })
            .catch(function (error) {
                console.log(error);
            });
    }
    checkIfDateActived() {

    }
    componentDidMount() {
        this.monthChange(0)
        this.getItems()
    }
    render() {
        return (
            <section className='calender_container'>
                <div>預約日期與時段</div>
                <div><button className='month_change_btn' onClick={() => this.monthChange(-1)}>&lt;</button><span>{this.state.year}年{this.state.month}月</span><button className='month_change_btn' onClick={() => this.monthChange(1)} >&gt;</button></div>
                <div>
                    <div className="day_picker_row">
                        <div className="day_picker_box">日</div>
                        <div className="day_picker_box">一</div>
                        <div className="day_picker_box">二</div>
                        <div className="day_picker_box">三</div>
                        <div className="day_picker_box">四</div>
                        <div className="day_picker_box">五</div>
                        <div className="day_picker_box">六</div>
                    </div>
                    {this.state.allMonthDayList.map((singleWeek, weekIndex) =>
                        <div className="day_picker_row" key={weekIndex}>
                            {singleWeek.map((singleDay, dayIndex) =>
                                singleDay !== null ?
                                    <div className='day_picker_box' key={dayIndex}>
                                        <button className={this.state.activedDay.indexOf(singleDay.toString()) !== -1 ? 'day_picker_btn_actived' : 'day_picker_btn'} onClick={() => this.handleClickDay(singleDay)}>
                                            {singleDay}
                                        </button>
                                    </div> :
                                    <div className='day_picker_box' key={dayIndex}>{}</div>
                            )}
                        </div>)}
                </div>

                <section>
                    {this.state.dateData.length !== 0 ?
                        <div>
                            <div>{`${this.props.time1_name}剩(${this.state.dateData[0]['time1_remaining']}位)`}</div>
                            <div>{`${this.props.time2_name}剩(${this.state.dateData[0]['time2_remaining']}位)`}</div>
                            <div>{`${this.props.time3_name}剩(${this.state.dateData[0]['time3_remaining']}位)`}</div>
                        </div> : ''
                    }
                </section>
                <div>
                    立即結帳
                </div>
            </section >
        )
    }
}
