import React, { Component } from 'react';
import '../css/Footer.scss'
class Footer extends Component {
  render() {
    return (
      <footer className="container-fluid">
        <section className="width80">
          <section className="d-sm-flex d-none container">
            <div className="row">
              <div className="col-4">
                <img src={require('../image/LOGO.svg')} alt="" />
                <p>體驗，遇見更美好的自己</p>
                <p>
                  Niceday 玩體驗成立於 2013年，提供特色體驗預訂服務，我們將台灣好玩的各種體驗發掘出來，提供給喜愛台灣、熱愛生活的人。</p>
              </div>
              <ul className="col-2">
                <li>認識我們</li>
                <li>關於 Niceday 玩體驗</li>
                <li>精選主題特輯</li>
                <li>媒體報導</li>
              </ul>
              <ul className="col-2">
                <li>加入我們</li>
                <li>體驗商加入</li>
                <li>駐站部落客</li>
                <li>私人或企業包場</li>
                <li>徵才資訊</li>
              </ul>
              <ul className="col-2">
                <li>追蹤我們</li>
                <li>Facebook</li>
                <li>Instagram</li>
                <li>Line</li>
              </ul>
              <ul className="col-2">
                <li>網站資訊</li>
                <li>常見問題 FAQ</li>
                <li>服務條款</li>
                <li>隱私權條款</li>
              </ul>
            </div>
          </section>
          <section className="row d-sm-none d-flex">
          </section>
        </section>
      </footer>
    );
  }
}

export default Footer;
