import React, { Component, Fragment } from 'react'
import axios from 'axios'
import htmlParse from 'html-react-parser';
import './css/product.scss'
import Calender from './components/Calender.js'

export default class category extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      sub_product_index: 0
    }
    this.product_id = this.props.match.params.product_id
  }
  componentDidMount() {
    const self = this
    axios.get(process.env.REACT_APP_BACKHOST + '/product/' + self.product_id)
      .then(response => {
        self.setState({ data: response.data })
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  render() {
    if (this.state.data.length !== 0) {
      let data = this.state.data[0]
      let dataSelect = this.state.data[this.state.sub_product_index]
      return (
        <article className='product_wrapper'>
          <section className='category-banner'></section>
          <section className='container'>
            <div className="row">
              <section className='col-7'>
                <section className='mb-5'>
                  <a href={`/area/${data.area_id}`} className='tag-button mr-2'>{data.area_name}</a><a href={`/category/${data.category_id}`} className='tag-button'>{data.category_name}</a>
                </section>
                <article>
                  <section>
                    <h1 className='product_title'>{data.product_name}</h1>
                    <div className='mb-4'><span className='product_introduce product_subtitle'>簡介</span><button className='product_introduce_btn'><i className="fas fa-thumbs-up"></i> 讚 639</button><button className='product_introduce_btn'>分享</button>
                    </div>
                    <p>{htmlParse(data.product_content)}</p>
                  </section>
                  <section>
                    <div className='text-center'>
                      <button className='show_complete'>顯示完整內容</button></div>
                    <div>
                      <div className="product_subtitle">選擇產品</div>
                      <ul>
                        {this.state.data.map((item, index) =>
                          <li className='choose_product_li' key={item.sub_product_id}>
                            <input type="radio" name='sub_product_select' id={`sub_select_${item.sub_product_name}`} /><label htmlFor={`sub_select_${item.sub_product_name}`}>{item.sub_product_name}</label>
                          </li>)}
                      </ul>
                    </div>
                    <div>
                      <div className="product_subtitle">價目表</div>
                      <ul className='ml-4'>
                        {JSON.parse(dataSelect['price_list']).map((item, index) =>
                          <li className='price_list_li' key={index}>
                            <span className='price_list_li_name'>{item.name}</span>
                            <span className='price_list_li_price'>{item.price}</span>
                          </li>)}
                      </ul>
                    </div>
                    <div className='exprience_including'>
                      <div className="product_subtitle">體驗包含</div>
                      <ul className='ml-5'>
                        {JSON.parse(dataSelect['exprience_including']).map(item => <li key={item} className='exprience_including_li'>{item}</li>)}
                      </ul>
                    </div>
                    <div className='detail_explain'>
                      <div className="product_subtitle">詳細說明</div>
                      <ul className='ml-5'>
                        {JSON.parse(dataSelect['detail_explain']).map((item, index) => <li key={index} className='detail_explain_li'>{item.name}:{htmlParse(item.content)}</li>)}
                      </ul>
                    </div>
                    <div className='cancel_or_change'>
                      <div className="product_subtitle">取消與更改
                      <div>

                        </div>
                      </div>
                    </div>
                    <div className='exprience_provider'>
                      <div className="product_subtitle">體驗提供商</div>
                      <div><a href={`/supplier/${data.supplier_id}`}>{data.supplier_name}</a></div>
                    </div>
                  </section>
                </article>
              </section>
              <section className='col-5'>
                <section>
                  <div className='product_price'>${data.price}起</div>
                  <ul className='prodiuct_price_content'>
                    <li><i className="fas fa-map-marker-alt"></i>體驗地點:{data.area_name}</li>
                    <li>付款方式 : 信用卡 / ATM / ApplePay / LinePay</li>
                    <li>信用卡三期 0 利率 (18家)</li>
                    <li>可累積「亞洲萬里通」里數 ( 詳細說明 )</li>
                    <li className='ml-4'>無法與其他優惠代碼、紅利金同時使用</li>
                  </ul>
                  <div className='text-center'><button className='book_btn'>體驗預定</button></div>
                </section>
                <Calender
                  time1_name={dataSelect['time1_name']}
                  time2_name={dataSelect['time2_name']}
                  time3_name={dataSelect['time3_name']}
                  sub_product_id={dataSelect['sub_product_id']} />
              </section>
            </div>
          </section>
        </article>
      )
    } else {
      return (<div className='flex-center w-100 h-100'>LOADING</div>)
    }
  }
}
