import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import '../css/nav.scss'

class Menu extends Component {
  constructor(props) {
    super(props)
    this.state = {
      hoverIndex: 1,
      mobileAsideMenuIndex: 1,
      categoryData: [
        { id: 1, name: '查看全部體驗' },
        { id: 2, name: '團體遊戲', subCategory: [{ id: 1, name: '密室逃脫' }] },
      ]
    }
  }
  render() {
    const data = this.state.categoryData
    if (!this.props.isMobile) {
      return (
        <div className="desktop-menu-group">
          <ul>
            {data.map((category, index) =>
              <li key={category.id} onMouseEnter={() => this.setState({ hoverIndex: index })} >
                <a href="" className='d-block w-100 h-100'>{category.name}</a>
              </li>
            )}
          </ul>
          {data[this.state.hoverIndex].subCategory ?
            <ul>
              {data[this.state.hoverIndex].subCategory.map(subCategory =>
                <li key={subCategory.id}>
                  <Link to={`/category/${subCategory.id}`} className='d-block w-100 h-100'> {subCategory.name}</Link>
                </li>
              )}
            </ul> : <div></div>
          }
        </div>
      )
    } else {
      return (
        <div className="mobile-aside-menu-group">
          <input type="checkbox" id='mobile-aside-sub-menu-toggle' className='mobile-aside-sub-menu-toggle-checkbox' />
          <ul className='mobile-aside-menu'>
            <li>探索</li>
            <li>體驗類別</li>
            {data.map((item, index) => <li className='mobile-aside-menu-link' key={item.id}><label onClick={() => this.setState({ mobileAsideMenuIndex: index })} htmlFor='mobile-aside-sub-menu-toggle'>{item.name}</label></li>)}

            <li className=''>認識我們 +</li>
            <li>加入我們 +</li>
            <li>網站資訊 +</li>
          </ul>
          <ul className='mobile-aside-sub-menu'>
            <li><label htmlFor='mobile-aside-sub-menu-toggle'>&#60;</label><span>{data[this.state.mobileAsideMenuIndex].name}</span></li>
            {data[this.state.mobileAsideMenuIndex].subCategory &&
              data[this.state.mobileAsideMenuIndex].subCategory.map(item =>
                <li key={item.id} className='mobile-aside-menu-link-sub' y={item.id}>{item.name}</li>)}
          </ul>
        </div>
      )
    }

  }
}

class Nav extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isNavHover: false,
      isMobileAsideShow: false,
      whichCategoryHover: '',
    }
  }
  render() {
    return (
      <header>
        <section className="desktop-nav d-sm-flex container d-none">
          <a href=""><img src={require('../image/LOGO.svg')} /></a>
          <nav className="flex-center menu-text position-relative">
            <a href="">探索體驗</a> <i className="fas fa-caret-down"></i>
            <Menu isMobile={false} />
          </nav>
          <div className="search-outbox"><input type="text" placeholder="想玩甚麼體驗" /><i className="fas fa-search"></i></div>

          <a className="ml-auto flex-center" href="#">登入/註冊</a>
        </section>
        <section className="mobile-nav d-sm-none d-block w-100">
          <div className='nav-button-group'>
            <button className='bars' onClick={() => { this.setState({ isMobileAsideShow: true }) }}><i className="fas fa-bars"></i></button>
            <button className='search'><i className="fas fa-search"></i></button>
            <button className='user'><i className="fas fa-user"></i></button>
            <img src={require('../image/LOGO.svg')} />
          </div>
          <div className={`mobile-aside-group ${this.state.isMobileAsideShow ? 'show' : ''} `}>
            <aside className='mobile-aside'>
              <Menu isMobile={true} closeAside={() => this.setState({ isMobileAsideShow: false })} />
            </aside>
            <button className='mobile-aside-close' onClick={() => this.setState({ isMobileAsideShow: false })}>
            </button>
          </div>

        </section>
      </header>
    );
  }
}

export default Nav;
