import React from 'react';
import { connect } from 'react-redux'
import './css/SingleCard.scss'
function SingleCard(props) {
    const imgUrl = require(`./image/Home/${props.type}${props.id}.jpg`)
    const data = props.data[props.type].filter(item => item.id === props.id)[0]
    return (
        <section className={props.type === 'hot'?"col-6 col-md-3 mb-3 single-card-components":'w245 single-card-components'}>
            <a href='#' className="single-card">
                <div><img src={imgUrl} alt="" className='w-100' /></div>
                <div className="hover-rise mt-auto">
                    <div className="text-section">
                        <h5 className="card-title p-4">{data.title}
                            <div className="card-content px-4 h6">{data.content}</div>
                        </h5>
                    </div>
                    <div className="card-footer">
                        <div><i className="fas fa-map-marker-alt mr-2"></i>{data.city}</div><div>${data.price}起</div>
                    </div>
                </div>
            </a>
        </section>
    )
}

const mapStateToProps = (state) => ({ data: state.homeData })
export default connect(mapStateToProps)(SingleCard)